import React, { Component } from 'react'

class List extends Component {

  render() {
    return (
      <div className="grid">
        <ul className="list-inline">
          { this.renderItems() }
        </ul>
      </div>
    );
  }

  renderItems() {
    const { items, emptyTemplate, itemRendered } = this.props;
    return items.length ? items.map(itemRendered) : emptyTemplate; 
  }
}

export default List;
